header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    padding: 0px 50px;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
}

header img{
    width: 100px;
}

header input {
max-width: 300px;
width: 100%;
background-color: #ffffff00;
z-index: 1;
}

header i{
 position: absolute;
 left: 20px ;
}

#input_div{
    position: relative;
}

::placeholder{
    color: black
}

header div{
    display: flex;
    flex-direction: row;
    gap: 0px;
    align-items: center;
}

header a{
color: black;
background-color: #ffffff00;
border: none;
cursor: pointer;
font-size: 14px;
padding: 0px 20px;

}
/*header a:first-child{
    border-right: 3px solid #00000020;
    height: 20px;

}*/
header a:last-child{
    background-color: black;
    padding: 13px 30px ;
    color: white;
    border-radius: 50px;
}

